import React from "react";
import {connect} from "react-redux";
import {
    getCurrentSelectedAvailableProductsKey,
    getSearchValue,
    getSelectedOfferTypeDescriptions,
    getSelectedSoftBundleGroup,
} from "eshop/modules/simfree/selectors";
import {getAddTerminalToOfferBundleNo, getEntryUnderChange} from "eshop/modules/cart/selectors";
import {
    getClientContext,
    getExtProcessSelectConfig,
    getMarketContext,
    getProcessTypeFiltersForSelect,
    getSelectedLoyaltyLengthValue,
    getSelectedOfferType,
    getSelectedProcessTypeValue,
    getSortedLoyaltyLengthFiltersWithIndefinitePeriodForSelect,
} from "eshop/modules/configurator/selectors/filters";
import {
    getContractRole,
    getContractRoleInProgress,
    getCurrentDeviceInstalmentsCountValue,
    getDeviceInstalmentsCountForSelect,
    getLongDescriptionTableForSelectedOffer,
    getMnpPriceDescriptionForSelectedOffer,
    getNameForSelectedOffer,
    getOffersDataInContext,
    getSelectedOffer,
    getSelectedOfferId,
    getSelectedOfferRatePlanName,
} from "eshop/modules/configurator/selectors/offers";
import {selectProductListLoyaltyLength, selectProductListProcessType} from "eshop/modules/configurator/actions/filters";
import {
    setDeviceInstalmentsConfiguration,
    setSelectedDeviceInstalmentsCountForProductList,
    setSelectedOfferForProductList,
} from "eshop/modules/configurator/actions/offers";
import {getOffersLoading} from "eshop/modules/configurator/selectors/metaData";
import {changeSearchDeviceValue} from "eshop/modules/simfree/actions/filter";
import TransactionProcessType from "../../../core/constants/TransactionProcessTypeEnum";
import MarketSegment from "eshop/modules/core/enum/MarketSegment";
import * as PropTypes from "prop-types";
import {getStayLoveRetentionMSISDN} from "../../../auth/selectors/authorization";
import {isDuet} from "../../selectors";
import OfferType from "../../../core/constants/OfferTypeEnum";

class OfferDeviceFilterComponent extends React.Component {

    constructor(props) {
        super(props);
        this.selectProcessType = this.selectProcessType.bind(this);
        this.selectLoyaltyLength = this.selectLoyaltyLength.bind(this);
        this.selectOffer = this.selectOffer.bind(this);
        this.selectDeviceInstalmentsCount = this.selectDeviceInstalmentsCount.bind(this);
        this.changeSearchDeviceValue = this.changeSearchDeviceValue.bind(this);
        this.getSelectedExtProcessSelectIndexes = this.getSelectedExtProcessSelectIndexes.bind(this);
        this.getExtProcessSelectForLevel = this.getExtProcessSelectForLevel.bind(this);
        this.selectProcessForExtProcessSelect = this.selectProcessForExtProcessSelect.bind(this);
        this.getInfo = this.getInfo.bind(this);

        console.warn("^^^^^^^^^^^^^^^^^^^^OfferDeviceFilterComponent constructor props^^^^^^^^^^^^^^^^^^^^");
        console.warn(this.props);
    }

    componentWillMount(){
        if(this.props.deviceInstalmentsConfiguration){
            this.props.setDeviceInstalmentsConfiguration(this.props.deviceInstalmentsConfiguration)
        }
    }


    selectProcessType(e) {
        this.props.selectProcessType(e.value)
    }

    selectLoyaltyLength(e) {
        this.props.selectLoyaltyLength(e.value)
    }

    selectOffer(value) {
        this.props.selectOffer(value)
    }

    selectDeviceInstalmentsCount(e) {
        this.props.selectDeviceInstalmentsCount(e.value)
    }

    getProcessLabel(){
        if(this.props.selectedOfferType===OfferType.DATA)
            return "Internet mobilny";
        if(this.props.selectedOfferType===OfferType.VOICE)
            return "Abonament komórkowy";
        return "Proces";
    }

    changeSearchDeviceValue(e) {
        this.props.changeSearchDeviceValue(e.value);
    }
    disabledProcessesByStayLove(){
        if(this.props.stayLoveRetentionMSISDN && this.props.selectedOfferType===OfferType.VOICE_LDF){
            console.log("OPTIONS TO DISABLE ",this.props.processTypesForSelect);

            let optionsToDisable=[];
            this.props.processTypesForSelect.forEach(
                option => option.value!==TransactionProcessType.RETENTION && optionsToDisable.push(option));
            console.log("OPTIONS TO DISABLE ",optionsToDisable);
            return optionsToDisable;

        }
        return [];

    }
    excludeProcessesFromDevicesFilter(){
        const processTypesToExclude = [
            TransactionProcessType.ASSIGNMENT,TransactionProcessType.ASSIGNMENT_DEATH,
            TransactionProcessType.ASSIGNMENT_B2C_B2B, TransactionProcessType.ASSIGNMENT_B2C_JDG
        ];
        const processTypesToExcludeByStayLove=this.disabledProcessesByStayLove();
console.log("processTypesToExcludeByStayLove",processTypesToExcludeByStayLove);
        return this.props.processTypesForSelect &&
            this.props.processTypesForSelect.filter(
                processType => processTypesToExclude.indexOf(processType.value) === -1
                    && processTypesToExcludeByStayLove.filter(
                        processTypeNotStayLove=>processTypeNotStayLove.value==processType.value).length===0);
    }

    getPropositionDetails(){
        let entryUnderChange = this.props.entryUnderChange;
        if(entryUnderChange){
            if(this.props.propositionDetails){ //this condition is a dirty hack to delay entryUnderChange.details render
                try {
                    return JSON.parse(entryUnderChange.details)
                } catch(err) {
                    console.log("## details on cart parsing error##")
                }
            }
        } else {
            return this.props.propositionDetails
        }
    }

    getSelectedExtProcessSelectIndexes() {
        return this.getSelectedExtProcessSelectIndexesForOption(this.props.extProcessSelectConfig, -1, this.props.selectedProcessValue);
    }

    getSelectedExtProcessSelectIndexesForOption(option, index, process) {
        if (option.options && option.options.length > 0) {
            return this.getSelectedExtProcessSelectIndexesForOptions(option.options, index, process);
        }
        if (option.process === process) {
            return [index];
        }
        return false;
    }

    getSelectedExtProcessSelectIndexesForOptions(options, index, process) {
        let found = options
            .map((options, index) => this.getSelectedExtProcessSelectIndexesForOption(options, index, process))
            .find(result => result);
        if (found) {
            return this.getSelectedIndex(found, index);
        }
        return false;
    }

    getSelectedIndex(found, index) {
        if(index !== -1) {
            return [index, ...found];
        }
        return found;
    }

    getExtProcessSelectForLevel(level) {
        const selectedExtProcessSelectIndexes = this.getSelectedExtProcessSelectIndexes();
        let extProcessSelect = this.props.extProcessSelectConfig;
        for (let i = 0; i < level; i++) {
            extProcessSelect = extProcessSelect.options[selectedExtProcessSelectIndexes[i]];
        }
        return extProcessSelect;
    }

    selectProcessForExtProcessSelect(extProcessSelect) {
        return e => {
            const process = this.getProcessForExtProcessSelect(extProcessSelect.options[e.value]);
            this.props.selectProcessType(process);
        }
    }

    createOptionsForExtProcessSelect(extProcessSelect) {
        return extProcessSelect.options.map((option, index) => ({
            value: index,
            description: option.description,
        }));
    }

    getProcessForExtProcessSelect(extProcessSelect) {
        while (!extProcessSelect.process) {
            extProcessSelect = extProcessSelect.options[0];
        }
        return extProcessSelect.process;
    }

    getInfo() {
        return {
            label: this.props.descriptions[`infoDescription.${this.props.marketContext}.${this.props.selectedProcessValue}`],
            tooltip: this.props.descriptions[`infoTooltipDescription.${this.props.marketContext}.${this.props.selectedProcessValue}`],
        };
    }

    render() {
        return React.cloneElement(this.props.children, {
            processLabel: this.getProcessLabel(),
            header: this.props.descriptions.detailsPopupHeader,
            propositionName: this.props.propositionName,
            processTypesForSelect: this.excludeProcessesFromDevicesFilter(),
            loyaltyLengthsForSelect: this.props.loyaltyLengthsForSelect,
            loyaltyLength: this.props.loyaltyLength,
            deviceInstalmentsCountForSelect: this.props.deviceInstalmentsConfigurationForSelect,
            selectProcessCallback: this.selectProcessType,
            selectLoyaltyLengthCallback: this.selectLoyaltyLength,
            selectedProcessValue: this.props.selectedProcessValue,
            selectedLoyaltyLengthValue: this.props.selectedLoyaltyLengthValue,
            selectedDeviceInstalmentsCountValue: this.props.selectedDeviceInstalmentsCountValue,
            offers: this.props.offersInContext && this.props.offersInContext.offers,
            selectOfferCallback: this.selectOffer,
            selectDeviceInstalmentsCountCallback: this.selectDeviceInstalmentsCount,
            selectedOffer: this.props.selectedOffer,
            selectedOfferType: this.props.selectedOfferType,
            hide: !this.props.showProductListOfferFilters || this.props.selectedOfferType === OfferType.SIMFREE,
            detailsTable: this.getPropositionDetails(),
            entry: this.props.entryUnderChange,
            clientContextCheckboxConfig: getClientContextCheckboxConfig(this.props.filterCmsDescriptions),
            detailsLabelText: this.props.descriptions.detailLabelText,
            loading: this.props.offersLoading,
            mnpPriceDescription: this.props.mnpPriceDescription,
            selectedRatePlanName: this.props.selectedRatePlanName,
            selectedOfferObject: this.props.selectedOfferObject,
            clientContext: this.props.clientContext,
            contractRole: this.props.contractRole && this.props.contractRole[0],
            getContractRoleInProgress: this.props.getContractRoleInProgress,
            offerDiscountInfoDescription: this.props.offerDiscountInfoDescription,
            offerDiscountInfoConvDescription: this.props.offerDiscountInfoConvDescription,
            descriptions: this.props.descriptions,
            channels: this.props.channels,
            addTerminalToOfferBundleNo: this.props.addTerminalToOfferBundleNo,
            handleChangeSearchDeviceValue: this.changeSearchDeviceValue,
            searchValue: this.props.searchValue,
            isB2B: this.props.isB2B,
            extProcessSelectConfig: this.props.extProcessSelectConfig,
            getSelectedExtProcessSelectIndexes: this.getSelectedExtProcessSelectIndexes,
            getExtProcessSelectForLevel: this.getExtProcessSelectForLevel,
            createOptionsForExtProcessSelect: this.createOptionsForExtProcessSelect,
            selectProcessForExtProcessSelect: this.selectProcessForExtProcessSelect,
            info: this.getInfo(),
            softBundleGroup: this.props.softBundleGroup,
            stayLoveRetentionMSISDN: this.props.stayLoveRetentionMSISDN,
            isDuet: this.props.isDuet,
        });
    }
}

const getClientContextCheckboxConfig = desc => ({
    label: desc && desc.clientContextLabel || "Pokaż rabaty dla Klientów Orange",
    tooltip: desc && desc.clientContextTooltip || "Posiadając Orange Love możesz otrzymać rabat -20 zł za łącznie usług w Orange na Plany Komórkowe Standardowy, Optymalny, Wzbogacony. Rabat naliczany będzie co miesiąc (najpóźniej od 2-giej faktury po spełnieniu warunków promocji)."
});

const mapStateToProps = state => ({
    propositionName: getNameForSelectedOffer(state),
    propositionDetails: getLongDescriptionTableForSelectedOffer(state),
    processTypesForSelect: getProcessTypeFiltersForSelect(state),
    loyaltyLengthsForSelect: getSortedLoyaltyLengthFiltersWithIndefinitePeriodForSelect(state),
    selectedProcessValue:  getSelectedProcessTypeValue(state),
    selectedLoyaltyLengthValue: getSelectedLoyaltyLengthValue(state),
    selectedDeviceInstalmentsCountValue: getCurrentDeviceInstalmentsCountValue(state),
    deviceInstalmentsConfigurationForSelect: getDeviceInstalmentsCountForSelect(state),
    offersInContext: getOffersDataInContext(state),
    selectedOffer: getSelectedOfferId(state),
    selectedOfferType: getSelectedOfferType(state),
    showProductListOfferFilters: getCurrentSelectedAvailableProductsKey(state),
    offersLoading: getOffersLoading(state),
    mnpPriceDescription: getMnpPriceDescriptionForSelectedOffer(state),
    selectedRatePlanName: getSelectedOfferRatePlanName(state),
    selectedOfferObject: getSelectedOffer(state),
    clientContext: getClientContext(state),
    contractRole: getContractRole(state),
    getContractRoleInProgress: getContractRoleInProgress(state),
    loyaltyLength: getSelectedLoyaltyLengthValue(state),
    filterCmsDescriptions: getSelectedOfferTypeDescriptions(state),
    addTerminalToOfferBundleNo: getAddTerminalToOfferBundleNo(state),
    searchValue: getSearchValue(state),
    entryUnderChange: getEntryUnderChange(state),
    isB2B: MarketSegment.isB2B(getMarketContext(state)),
    extProcessSelectConfig: getExtProcessSelectConfig(state),
    softBundleGroup: getSelectedSoftBundleGroup(state),
    marketContext: getMarketContext(state),
    stayLoveRetentionMSISDN: getStayLoveRetentionMSISDN(state),
    isDuet: isDuet(state),
});

const mapDispatchToProps = dispatch => ({
    selectProcessType: value => dispatch(selectProductListProcessType(value)),
    selectLoyaltyLength: value => dispatch(selectProductListLoyaltyLength(value)),
    selectOffer: value => dispatch(setSelectedOfferForProductList(value)),
    selectDeviceInstalmentsCount: value => dispatch(setSelectedDeviceInstalmentsCountForProductList(parseInt(value))),
    setDeviceInstalmentsConfiguration: value => dispatch(setDeviceInstalmentsConfiguration(value)),
    changeSearchDeviceValue: value => dispatch(changeSearchDeviceValue(value)),
});

OfferDeviceFilterComponent.PropTypes = {
    softBundleGroup: PropTypes.string,
};

export default connect(mapStateToProps, mapDispatchToProps)(OfferDeviceFilterComponent);

